// Object.assign (IE support)
if (typeof Object.assign === 'undefined') Object.assign = function() {
	let objs = arguments[0] && typeof arguments[0] === 'object' ? arguments[0] : {};

	for (let i = 1; i < arguments.length; i++) {
		if (typeof arguments[i] === 'object') {
			for (let key in arguments[i]) {
				objs[key] = arguments[i][key];
			}
		}
	}

	return objs;
};